"use client";

import { useQuery } from "@tanstack/react-query";
// import { Button } from "ui";
// import { Button } from "ui/src/stories/Button";
const fetchData = async () => {
  return [
    { id: 1, title: "Demo 1" },
    { id: 2, title: "Demo 2" },
    { id: 3, title: "Demo 3" }
  ];
};

export default function HomePage() {
  const { data, isLoading } = useQuery({
    queryKey: ["demoData"],
    queryFn: fetchData
  });

  if (isLoading) return <p>Cargando...</p>;

  return (
    <div className="p-6">
      <h1 className="text-3xl font-bold mb-4">Listado Demo</h1>
      <ul>
        {data?.map((item) => (
          <li key={item.id} className="mb-2 border p-2 rounded">
            {item.title}
          </li>
        ))}
      </ul>
      {/* <Button */}
      {/* <Button label="Click me" /> */}
    </div>
  );
}
